body {
    margin: 0px;
}

.main {
    display: -webkit-flex;
    display: flex;
}

.mainPanel {
    display: block;
    background-image: radial-gradient(circle, #040030, #030f49, #111664, #231b7f, #37209a);
    box-sizing: border-box;
    border: solid white ;
    border-width: 0px 2px 0px 0px;
    height: 100vh;
    width: 100%;
    min-width: 1024px;
}

.questionIndexPanel {
    display: block;
    background-image: radial-gradient(circle, #040030, #030f49, #111664, #231b7f, #37209a);
    width: 300px;
    min-width: 300px;
    max-width: 300px;
    height: 100vh;
}
.questionIndex {
    color: #ec9d00;
    list-style-type: none;
    font-size: 20px;
    font-family: "Gill Sans", sans-serif;
}

.dot::before {
    content: "";
    height: 5px;
    width: 5px;
    background-color: white;
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 0;
}

.highlight::before {
    box-shadow: 0 0 7px 1px white;
}

.question-box {
    position: relative;
    font-family: "Open Sans", sans-serif;
    text-transform: uppercase;
    font-weight: 800;
}

.active {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
}

.mark {
    color: white;
}

.active > .question-number {
    color: white;
}

.active > .question-value {
    color: black;
}

.question-number {
    padding-left: 15px;
    width: 30px;
    display: inline-block;
}

.question-value {
    padding-left: 15px;
    position: relative;
}
.help-wrap {
    margin: 15px;
}

.button-help {
    background-image: url(/ai-la-trieu-phu/static/media/butt_sprite.d06da0cc.png);
    width: 74px;
    height: 60px;
    display: inline-block;
    margin: 5px 3px;
}

.half-help {
    background-position: -100px -59px;
}

.half-help-hover:hover{
    background-position: -99px 1px;
}

.audience-help {
    background-position: 0px -59px;
}

.audience-help-hover:hover{
    background-position: 0px 1px;
}

.call-help {
    background-position: -201px -59px;
}

.call-help-hover:hover {
    background-position: -200px 1px;
}

.disable-help {
    opacity: 0.5;
}
.logo {
    margin: auto;
    display: block;
}

.question-answer {
    color: white;
    margin: 0 auto;
    width: 1000px;
}

.quest {
    background: url(data:image/png;base64,) center center/100% 100% no-repeat;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 18px;
    min-height: 80px;
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
    margin-bottom: 20px;
}

.quest span {
    display: inline-block;
    text-align: center;
    width: 60%;
}

.answer-row {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center center;
    margin-bottom: 10px;
    display: -webkit-flex;
    display: flex;
}

.answer {
    width: 50%;
}

.answer-pointer {
    cursor: pointer;
}

.left {
    padding-left: 148px;
}

.right {
    padding-right: 118px;
}

.answer-inside {
    background-image: url(/ai-la-trieu-phu/static/media/sprite_ans.d981bfce.png);
    background-repeat: no-repeat;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 18px;
    line-height: 22px;
    height: 40px;
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
}

.answer-hover:hover {
    background-position: 0 -50px;
}

.answer-selected {
    background-position: 0 -50px;
}

.answer-correct {
    background-position: 0 -129px;
}

.choice {
    padding-left: 25px;
    color: #e5a934;
}

.answer-value {
    padding-left: 10px;
}

.start-button {
    background-image: url(/ai-la-trieu-phu/static/media/sprite_ans.d981bfce.png);
    background-repeat: no-repeat;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 18px;
    line-height: 22px;
    height: 40px;
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
    color: white;
    margin: 25px auto;
    width: 338px;
}

.start-button:hover {
    background-position: 0 -50px;
}

.start-button:active {
    opacity: 0.7;
}

.intro-video {
    position: fixed;
    z-index: 1;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}

.time-count {
    font-size: 50px;
    color: white;
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.won-notice {
    background: url(data:image/png;base64,) center center/100% 100% no-repeat;
    font-size: 70px;
    color: white;
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 60px;
}
.call-form {
    background-image: url(/ai-la-trieu-phu/static/media/form_phone.8426d178.png);
    background-repeat: no-repeat;
    width: 672px;
    height: 295px;
    margin: auto;
    margin-top: 140px;
}

.call-title {
    height: 39px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    font-size: 18px;
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
    color: white;
}

.call-count {
    font-family: Arial, Verdana, "Helvetica CY", sans-serif;
    color: white;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    height: 250px;
    font-size: 180px;
}

